import Vue from 'vue';
import App from './App';
import router from './router';
import vueResource from 'vue-resource'
import '@/common/js/media'

// 聚蓝领项目项目组件
// scss
import './common/css/julanling.scss'
// import './common/iconfont/iconfont.css'

Vue.use(vueResource);


Vue.config.productionTip = false;
/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    template: '<App/>',
    components: {
        App
    }
});



