import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

const lazyComponent = {
    circleOfWork: resolve => require(['@/components/circleOfWork/Main'], resolve),
    post: resolve => require(['@/components/circleOfWork/modules/Post'], resolve)
};

export default new Router({
    routes: [
        {
            path: "/circle",
            name: "circle",
            component: lazyComponent.circleOfWork,
            children: [
                {
                    path: "post",
                    name: "post",
                    component: lazyComponent.post
                }
            ]
        },
        {
            path: "*",
            redirect: "/circle/post"
        }
    ]
})



